import { Modal, Form, Card } from 'antd';
import * as React from 'react';
import { FormComponentProps } from 'antd/lib/form';
import { IReportAddParam } from '@/api';
import { toastformError } from '@/utils';

interface Props extends FormComponentProps {
  visible: boolean;
  onClose: (param: boolean) => any;
  projectId: number;
}

const ReportAddModel = (props: Props) => {
  const { getFieldDecorator } = props.form;
  React.useEffect(() => {
    props.form.resetFields();
  }, [props.visible]);
  const handleSubmit = (e: React.MouseEvent) => {
    e.preventDefault();
    props.form.validateFields((err, values) => {
      if (err) {
        toastformError(err);
        return;
      }
      props.onClose(false);
    });
  };
  const gridStyle = {
    width: '25%',
    'text-align': 'center'
  };
  return (
    <Modal onOk={handleSubmit} title='' visible={props.visible} onCancel={() => props.onClose(false)}>
      <Card>
        <Card.Grid style={gridStyle}></Card.Grid>
        <Card.Grid style={gridStyle}></Card.Grid>
        <Card.Grid style={gridStyle}></Card.Grid>
        <Card.Grid style={gridStyle}></Card.Grid>
      </Card>
    </Modal>
  );
};

export default Form.create<Props>()(ReportAddModel);
